import React, { Fragment, useState, useEffect } from 'react';
import './App.css';
import Header from './Components/Header'
import Footer from './Components/Footer'
import Content from './Components/Content'
import * as ARBApi from './utils/ARBApi';

export default function App() {
  const [seasonID, setSeasonID] = useState(0);
  const [seasons, setSeasons] = useState([]);
  const [players, setPlayers] = useState([]);
  const [matches, setMatches] = useState([]);

  useEffect(() => {
    if (seasonID === 0){
      ARBApi.getAll(`seasons`, seasonID).then((seasons) => {
        setSeasons(seasons);
      });
    }
  }, [seasonID]);

  const onChangeSeason = (seasonID) => {
    ARBApi.getAllByID(`players`, seasonID).then((players) => {
      setPlayers(players);
    });
    ARBApi.getAllByID(`matches`, seasonID).then((matches) => {
      setMatches(matches);
    });
    setSeasonID(seasonID);
  }

  return <Fragment>
      <Header title='Archiviorossoblu.it' subtitle='stiamo tornando'></Header>
        <Content seasons={seasons} players={players} matches={matches} changeSeasonDetails={onChangeSeason} seasonID={seasonID}></Content>
      <Footer title='Archiviorossoblu.it' copyright='Copyright 2020'></Footer>
    </Fragment>
  }
