import React, { Fragment } from 'react';
import {Grid, Paper} from '@material-ui/core';
import Seasons from './Seasons.js'
import SeasonDetails from './SeasonDetails.js'

export default function Content({seasons, players, matches, changeSeasonDetails, seasonID}) {

  const style = {
    Paper: {
      padding:20, 
      margin:10,
      height: 500,
      overflow: 'auto'
    }
  };

  const changeSeason = (seasonID) => {
    changeSeasonDetails(seasonID);
  }

  return (
    <Fragment>
      <Grid container spacing={2}>
        <Grid item xs={4}>
          <Paper elevation={3} style={style.Paper}>
            <Seasons seasons={seasons} onSeasonSelect={changeSeason}></Seasons>
          </Paper>
        </Grid>  
        <Grid item xs={8}>
          <Paper elevation={3} style={style.Paper}>
            <SeasonDetails players={players} matches={matches} seasonID={seasonID}></SeasonDetails>
          </Paper>     
        </Grid>  
      </Grid>
    </Fragment>
  );
}
